import React from "react";
import "./App.css";
import { Router, Route, HashRouter, Switch } from "react-router-dom";
import Header from "./header/Header";
import Home from "./pages/home/Home";
import NosProduits from "./pages/nosProduits/NosProduits";
import NousTrouver from "./pages/nousTrouver/NousTrouver";
import Recrutement from "./pages/recrutement/Recrutement";
import Footer from "./footer/Footer";
import history from "../history";

function App() {
  return (
    <div className="App">
      <Router history={history}>
        <Header />
        <Route path={process.env.PUBLIC_URL + '/'} exact component={Home} />
        <Route path={process.env.PUBLIC_URL + '/nos-produits'} exact component={NosProduits} />
        <Route path={process.env.PUBLIC_URL + '/nous-trouver'} exact component={NousTrouver} />
        <Route path={process.env.PUBLIC_URL + '/recrutement'} exact component={Recrutement} />
        <Footer />
      </Router>
    </div>
  );
}

export default App;
